<template>
  <div class="services-list">
    <div class="title">
      <h2 class="name">{{servicesListData.name}}</h2>
    </div>

    <div class="content">
      <div class="item" v-for="item in servicesListData.list" :key="item.id">
        <router-link to="/">
          {{item.title}}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    servicesListData: Object
  }
}
</script>

<style lang="stylus" scoped>
  .services-list
    .title
      height: 50px
      line-height: 50px
      .name
        margin-left: 20px
        color: #666
    .content
      overflow: hidden
      padding: 20px 30px
      background: #fff
      .item
        float: left
        width: 50%
        height: 50px
        line-height: 50px
        a
          color: #3c363e
        &:nth-child(even)
          text-indent: 10px
        
</style>





