import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/home'
import Phone from 'components/phone'
import Goods from 'components/goods'
import GoodsLists from 'components/goods/lists'
import Services from 'components/services'
import Shop from 'components/shop'
import Search from 'components/search'
import SearchLists from 'components/search/lists'
import Login from 'components/login/login'
import Cart from 'components/cart/cart'
import User from 'components/user/user'
import GoodDetail from 'components/detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/phone',
      component: Phone
    },
    {
      path: '/goods',
      component: Goods,
      children: [
        {
          path: '/',
          redirect: 'lists'
        },
        {
          path: 'lists',
          component: GoodsLists,
          children: [
            {
              path: '/',
              redirect: '0'
            },
            {
              name: 'lists',
              path: ':id',
              component: GoodsLists,
              children: [
                {
                  name: 'models',
                  path: ':model',
                  component: GoodsLists
                }
              ]
            }
          ]
        }
      ]
    },
    {
      path: '/services',
      component: Services
    },
    {
      path: '/shop',
      component: Shop
    },
    {
      path: '/search',
      component: Search,
      children: [
        {
          path: '/',
          redirect: 'general'
        },
        {
          path: ':type',
          component: SearchLists,
          children: [
            {
              path: '/',
              redirect: '0'
            },
            {
              name: 'list',
              path: ':id',
              component: SearchLists,
              children: [
                {
                  name: 'model',
                  path: ':model',
                  component: SearchLists
                }
              ]
            }
          ]
        }
      ]
    },
    {
      path: '/user',
      component: User,
      meta: { requireAuth: true } // 
    },
    {
      path: '/cart',
      component: Cart
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/detail/:goodId',
      component: GoodDetail
    }
  ]
})



