export const isShow = state => state.isShow

export const isWaiting = state => state.isWaiting

export const isGWaiting = state => state.isGWaiting

export const user = state => state.user

export const detailData = state => state.detailData

export const cartTotal = state => {
  let result = 0
  state.cartGoods.forEach((e, i) => {
    result += e.num
  })
  return result
}

export const totalPrice = state => {
  let result = 0
  state.cartGoods.forEach((e, i) => {
    if (e.check) {
      result += e.num * e.price
    }
  })
  return result.toFixed(2)
}

export const isCheckAll = state => {
  if(!state.cartGoods.length) return false
  let result = state.cartGoods.every((e, i) => {
    return e.check
  })
  return result
}


