<template>
  <div class="phone">
    <div class="phone-list">
      <div class="phone-item" v-for="item in PhoneImagesData" :key="item.id">
        <a :href="item.href" class="phone-link">
          <img v-lazy="item.image" class="phone-images">
          <div class="phone-text">
            <h3 class="name">{{item.name}}</h3>
            <p class="info">{{item.description}}</p>
            <p class="price">{{item.price}}</p>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    'PhoneImagesData': Array
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"

  .phone
    background: #fff
    .phone-list
      .phone-item
        .phone-link
          display: block
          padding: 20px
          overflow: hidden
          .phone-images
            float: left
            display: block
            width: 105px
          .phone-text
            float: right
            width: 170px
            margin-top: 16px
            .name
              margin-bottom: 10px;
              color: #2a242b
            .info
              margin-bottom: 10px;
              font-size: $font-size-medium
            .price
              font-size: $font-size-medium
              color: #f55669
          
</style>





