<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <!--  -->
      <slot></slot>
    </div>
    <div class="dots" v-show="dots.length !== 1">
      <span class="dot" v-for="(item,index) in dots" :class="{active:currentPageIndex===index}" :key="index"></span>
    </div>
  </div>
</template>

<script>
import BetterScroll from 'better-scroll'
import { addClass } from 'common/js/dom'

export default {
  data() {
    return {
      dots: [],
      currentPageIndex: 0
    }
  },
  props: {
    loop: { // 
      type: Boolean,
      default: true
    },
    autoPlay: { // 
      type: Boolean,
      default: true
    },
    interval: { // 
      type: Number,
      default: 4000
    },
    checkIndex: {
      type: Number
    }
  },
  mounted() {
    setTimeout(() => {
      this._setSliderWidth()
      this._initDots()
      this._initSlider()

      if (this.autoPlay) {
        this._play()
      }
    }, 20)

    window.addEventListener('resize', () => { // refresh()
      if (!this.slider) {
        return
      }
      this._setSliderWidth(true)
      this.slider.refresh()
    })
  },
  activated() { // keep-alive 
    if (!this.slider) {
      return
    }

    this._setSliderWidth(true)
    this.slider.refresh()
    if (this.autoPlay) {
      this._play()
    }
  },
  deactivated() { // keep-alive 
    clearTimeout(this.timer)
  },
  methods: {
    _setSliderWidth(isResize) {
      this.children = this.$refs.sliderGroup.children
      let width = 0
      let sliderWidth = this.$refs.slider.clientWidth
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        // class
        addClass(child, 'slider-item')
        child.style.width = sliderWidth + 'px'
        width += sliderWidth
      }
      // 
      if (this.loop && !isResize) {
        width += 2 * sliderWidth
      }
      this.$refs.sliderGroup.style.width = width + 'px'
      // console.log(this.$refs.sliderGroup)
    },
    _initDots() {
      this.dots = new Array(this.children.length)
    },
    _initSlider() { // 
      this.slider = new BetterScroll(this.$refs.slider, {
        scrollX: true,
        scrollY: false,
        momentum: false,
        snap: true,
        snapLoop: this.loop,
        snapThreshold: 0.3,
        snapSpeed: 400,
        eventPassthrough: 'vertical'
      })
      this.slider.on('scrollEnd', () => {
        let pageIndex = this.slider.getCurrentPage().pageX
        if (this.loop) {
          pageIndex -= 1
        }
        this.currentPageIndex = pageIndex
        if (this.autoPlay) {
          clearTimeout(this.timer)
          this._play()
        }
      })
    },
    _play() {
      let pageIndex = this.currentPageIndex + 1
      if (this.loop) {
        pageIndex += 1
      }
      this.timer = setTimeout(() => {
        this.slider.goToPage(pageIndex, 0, 400)
      }, this.interval)
    }
  },
  watch: {
    checkIndex(val) {
      this.slider.goToPage(val + 1, 0)
    }
  },
  destroyed() {
    clearTimeout(this.timer)
  }
}
</script>

<style lang="stylus" scoped>
  
  .slider
    min-height: 1px
    position: relative
    overflow: hidden
    .slider-group
      position: relative
      white-space: nowrap
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
        img
          display: block
          width: 100%
          
    .dots
      position: absolute
      left: 0
      right: 0
      bottom: 12px
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: rgba(164,164,164,0.5)
        opacity 0.5
        &.active
          width: 20px
          border-radius: 5px
          background: rgba(255,113,170,1)

</style>




