<template>
  <div class="search">

    <m-header>
      <router-link slot="search" tag="div" class="back" to="/">
        <i class="back-button iconfont icon-back"></i>
      </router-link>

      <h2 slot="search" class="title" @click="showMore">
        <span class="text" v-if="selectTitle.length">{{titleText}}</span>
        <span class="select-btn" ref="selectBtn" v-if="selectTitle.length">
          <i class="iconfont icon-select"></i>
        </span>
      </h2>

      <router-link slot="search" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
    </m-header>

    <transition name="fade">
      <div class="search-more" v-show="isShowMore">
        <div class="search-tip">
          <i class="iconfont icon-select tip-btn"></i>
        </div>
        <ul class="search-list">
          <li class="search-item" v-for="item in selectTitle" :key="item.id">
            <router-link :to="item.href.substr(20)">{{item.name}}</router-link>
          </li>
        </ul>
      </div>
    </transition>

    <div class="category-wrapper border-tb-1px" v-if="categoryData.length">
      <category>
        <ul class="category-list">
          <li class="category-title"></li>
          <li class="category-all">
            <router-link :to="{name:'list',params:{id:0}}"></router-link>
          </li>
          <li v-for="item in categoryData" :key="item.id">
            <router-link :to="{name:'list',params:{id:item.id}}">{{ item.name }}</router-link>
          </li>
        </ul>
      </category>
    </div>

    <div class="model-wrapper border-tb-1px" v-show="modelData[category]">
      <ul class="model-list">
        <li class="model-title"></li>
        <li class="model-all">
          <router-link :to="{name:'model',params:{model:0}}"></router-link>
        </li>
        <li v-for="item in modelData[category]" :key="item.id" class="model-item">
          <router-link :to="{name:'model',params:{model:item.id}}">{{item.name}}</router-link>
        </li>
      </ul>
    </div>

    <div class="erro-data" v-show="isShow">
      <div class="erro-image"></div>
      <p class="erro-text"></p>
    </div>

    <div class="list-wrap" ref="listWrap">
      <router-view :GoodsListImagesData="GoodsListImages" @sendLoading="getLoading"></router-view>
      <div class="loading-more" v-show="loadingShow">
        <div class="lds-css ng-scope loading-image">
          <div class="lds-spin" style="width:100%;height:100%">
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
            <div>
              <div></div>
            </div>
          </div>
        </div>
        <span class="loading-text">...</span>
      </div>
      <p class="loaded-text" v-show="loadedText"></p>
    </div>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import Category from 'components/search/category'
import { getSearchTitleData, getSearchListData, canceler } from 'api'

export default {
  name: 'search',
  data() {
    return {
      isShowMore: false,
      categoryData: [],
      modelData: {},
      category: 0,
      adaptation: 0,
      page: 1,
      totalPage: 0,
      categoryId: [],
      GoodsListImages: [],
      isShow: false,
      loadingShow: false,
      loadedText: false,
      listWrapScrollTop: 0,
      selectTitle: [],
      type: '',
      titleText: ''
    }
  },
  created() {
    this.type = this.$route.params.type
    this._getSearchTitleData(this.type)
  },
  mounted() {
    this.$refs.listWrap.addEventListener('scroll', this.listWrapScroll)
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$el.nextElementSibling.nextElementSibling.style.display = 'block'
    })
  },
  methods: {
    showMore() {
      if (this.isShowMore) {
        this.isShowMore = false
        this.$refs.selectBtn.style.cssText = ''
      } else {
        this.isShowMore = true
        this.$refs.selectBtn.style.cssText = 'transform: rotate(180deg); line-height:22px;'
      }
    },
    _getSearchTitleData(type) {
      this.$store.dispatch('waitingStart', true)
      getSearchTitleData(type).then((res) => {
        this.categoryData = res.list
        this.selectTitle = res.title
        this.selectTitle.forEach((e, i) => { // header
          if (e.href.substr(28) === this.type) {
            this.titleText = e.name
          }
        })
        res.list.forEach((e, i) => {
          if (e.adaptation_phones.length) {
            this.categoryId.push(e.id)
            this.modelData[e.id] = e.adaptation_phones
          }
        })
        this.category = this.$route.params.id
        if (this.$route.params.model) {
          this.adaptation = this.$route.params.model
        } else {
          this.adaptation = 0
        }
        this._getSearchListData(this.type, this.category, this.adaptation, this.page)
      })
    },
    _getSearchListData(type, category, adaptation, page) {
      if (this.page > this.totalPage && this.totalPage) {
        this.loadingShow = false
        this.loadedText = true
        this.$el.nextElementSibling.nextElementSibling.style.display = 'block'
        setTimeout(() => {
          this.listWrapScrollTop = this.$refs.listWrap.scrollHeight - this.$refs.listWrap.offsetHeight
        }, 0)
        return
      }
      getSearchListData(type, category, adaptation, page).then((res) => {
        if (res && res.status === 'ok') {
          this.$store.dispatch('waitingEnd', false)
          this.page++
          this.totalPage = res.result.total_page
          res.result.list.forEach((e) => {
            this.GoodsListImages.push(e)
          })
          if (this.GoodsListImages.length === 0) {
            this.isShow = true
          }
          console.log(this.GoodsListImages.length)
          this.loadingShow = false
          this.setListWrapHeight()
        }
      })
    },
    setListWrapHeight() {
      let re = /\d+/g
      let arr = window.location.hash.match(re)
      if (!arr) return
      if (!this.$refs.listWrap) return
      if (arr.length === 1 && this.GoodsListImages.length > 4) {
        this.$refs.listWrap.style.height = window.innerHeight - 94 + 'px'
      }
      if (arr.length === 2 && this.GoodsListImages.length > 4) {
        this.$refs.listWrap.style.height = window.innerHeight - 138 + 'px'
      }
    },
    getLoading(data) {
      if (this.totalPage === 0) { // 
        this.$el.nextElementSibling.nextElementSibling.style.display = 'block'
        return
      }
      if (data) {
        this.loadingShow = data
        this._getSearchListData(this.type, this.category, this.adaptation, this.page)
      }
    },
    listWrapScroll() {
      if (this.listWrapScrollTop) {
        let difference = this.listWrapScrollTop - this.$refs.listWrap.scrollTop
        if (difference) {
          window.scrollTo(0, -40)
        }
      }
    }
  },
  computed: {
    searchSelectData() {
      return this.$store.state.searchSelectData
    }
  },
  watch: {
    '$route'(to, from) {
      let re = /\d+/g
      let arr = window.location.hash.match(re)
      if (this.categoryId.includes(arr[0]) && arr.length === 1) {
        this.$router.push({ name: 'model', params: { model: 0 } }) // 
        return // 
      }
      this.category = this.$route.params.id
      if (this.$route.params.model || this.$route.params.model === 0) {
        this.adaptation = this.$route.params.model
      } else {
        this.adaptation = 0
      }
      this.page = 1
      this.GoodsListImages = []
      this.isShow = false
      this.loadedText = false
      this.totalPage = 0
      this.listWrapScrollTop = 0
      this.$refs.listWrap.removeAttribute('style')
      if (this.$route.params.type !== this.type) { // select
        this.type = this.$route.params.type
        this.categoryData = []
        this._getSearchTitleData(this.type)
        this.isShowMore = false
        this.$refs.selectBtn.style.cssText = ''
        return // SearchListData
      }
      this.$store.dispatch('waitingStart', true)
      canceler()
      this._getSearchListData(this.type, this.category, this.adaptation, this.page)
    }
  },
  components: {
    MHeader,
    Category
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .search
    .m-header
      position: relative
      .back
        float: left
        width: 50px
        height: 50px
        line-height: 50px
        text-align: center
        .back-button
          font-size: $font-size-large-x
      .title
        position: absolute
        left: 0
        top: 0
        right: 0
        bottom: 0
        margin: auto
        width: 180px
        height: 50px
        line-height: 50px
        text-align: center
        .text
          font-size: $font-size-large
          color: #3c363e
        .select-btn
          display: inline-block
          width: 20px
          height: 20px
          line-height: 20px
          i
            font-size: $font-size-large
      .home
        float: right
        width: 50px
        height: 50px
        line-height: 50px
        text-align: center
        .icon-home
          font-size: $font-size-large-x

    .search-more
      position: absolute
      left: 0
      top: 50px
      right: 0
      bottom: 0
      margin: 0 auto
      width: 150px
      height: 120px
      background: #fff
      border: 1px solid #eee
      border-radius: 4px
      z-index: 1
      .search-tip
        position: absolute
        left: 67px
        top: -12px
        transform: rotate(180deg)
        background: #fff
        .tip-btn
          display: block
          color #eee
      .search-list
        .search-item
          height: 40px
          line-height: 40px
          text-align: center
        a
          color: #3c363e
          font-size: 15px
          &.router-link-active
            color: #f55669

    .category-wrapper
      background: #fff
      border-top-1px(#f6f4f8)

    .model-wrapper
      background: #fff
      border-bottom-1px(#f6f4f8)
      .model-list
        padding: 10px 0
        overflow: hidden
        .model-title
          float: left
          padding: 4px 0 4px 10px
          color: #444
        .model-all
          float: left
          margin-right: 2px
          a
            display: block
            padding: 4px
            color: #444
            &.router-link-active
              background: #f55669
              color: #fff
              border-radius 4px
        .model-item
          float: left
          margin-right: 2px
          a
            display: block
            padding: 4px
            color: #444
            &.router-link-active
              background: #f55669
              color: #fff
              border-radius 4px
          &:last-child
            margin-right: 0

    .erro-data
      padding: 50px 0
      background: #fff
      .erro-image
        width: 140px
        height: 105px
        margin: 0 auto 10px
        bg-image('pic_error')
        background-size: 140px 105px
        background-repeat: no-repeat
      .erro-text
        text-align: center
        font-size: 15px
        color: #3c363e

    .list-wrap
      overflow: scroll
      -webkit-overflow-scrolling: touch // ISO
      .loading-more
        text-align: center
        padding: 10px 0
        .loading-image
          display: inline-block
          vertical-align: middle
        .loading-text
          display: inline-block
          vertical-align: middle
          font-size: 14px
          color: #b6a6c6

      .loaded-text
        height: 40px
        line-height: 40px
        text-align: center
        color: #b6a6c6
        font-size: 14px

  .fade-enter-active, .fade-leave-active // 
    transition: all 0.5s
  .fade-enter, .fade-leave-to // 
    opacity: 0

  .lds-spin
    position: relative

  .lds-spin div>div
    position: absolute
    width: 28px
    height: 28px
    border-radius: 50%
    background: #f55669
    -webkit-animation: lds-spin 1s linear infinite
    animation: lds-spin 1s linear infinite

  .lds-spin div:nth-child(1)>div
    left: 146px
    top: 86px
    -webkit-animation-delay: -0.875s
    animation-delay: -0.875s

  .lds-spin>div:nth-child(1)
    -webkit-transform: rotate(0deg)
    transform: rotate(0deg)
    -webkit-transform-origin: 160px 100px
    transform-origin: 160px 100px

  .lds-spin div:nth-child(2)>div
    left: 128.42640686px
    top: 128.42640686px
    -webkit-animation-delay: -0.75s
    animation-delay: -0.75s

  .lds-spin>div:nth-child(2)
    -webkit-transform: rotate(45deg)
    transform: rotate(45deg)
    -webkit-transform-origin: 142.42640686px 142.42640686px
    transform-origin: 142.42640686px 142.42640686px

  .lds-spin div:nth-child(3)>div
    left: 86px
    top: 146px
    -webkit-animation-delay: -0.625s
    animation-delay: -0.625s

  .lds-spin>div:nth-child(3)
    -webkit-transform: rotate(90deg)
    transform: rotate(90deg)
    -webkit-transform-origin: 100px 160px
    transform-origin: 100px 160px

  .lds-spin div:nth-child(4)>div
    left: 43.57359314px
    top: 128.42640686px
    -webkit-animation-delay: -0.5s
    animation-delay: -0.5s

  .lds-spin>div:nth-child(4)
    -webkit-transform: rotate(135deg)
    transform: rotate(135deg)
    -webkit-transform-origin: 57.57359314px 142.42640686px
    transform-origin: 57.57359314px 142.42640686px

  .lds-spin div:nth-child(5)>div
    left: 26px
    top: 86px
    -webkit-animation-delay: -0.375s
    animation-delay: -0.375s

  .lds-spin>div:nth-child(5)
    -webkit-transform: rotate(180deg)
    transform: rotate(180deg)
    -webkit-transform-origin: 40px 100px
    transform-origin: 40px 100px

  .lds-spin div:nth-child(6)>div
    left: 43.57359314px
    top: 43.57359314px
    -webkit-animation-delay: -0.25s
    animation-delay: -0.25s

  .lds-spin>div:nth-child(6)
    -webkit-transform: rotate(225deg)
    transform: rotate(225deg)
    -webkit-transform-origin: 57.57359314px 57.57359314px
    transform-origin: 57.57359314px 57.57359314px

  .lds-spin div:nth-child(7)>div
    left: 86px
    top: 26px
    -webkit-animation-delay: -0.125s
    animation-delay: -0.125s

  .lds-spin>div:nth-child(7)
    -webkit-transform: rotate(270deg)
    transform: rotate(270deg)
    -webkit-transform-origin: 100px 40px
    transform-origin: 100px 40px

  .lds-spin div:nth-child(8)>div
    left: 128.42640686px
    top: 43.57359314px
    -webkit-animation-delay: 0s
    animation-delay: 0s

  .lds-spin>div:nth-child(8)
    -webkit-transform: rotate(315deg)
    transform: rotate(315deg)
    -webkit-transform-origin: 142.42640686px 57.57359314px
    transform-origin: 142.42640686px 57.57359314px

  .lds-spin div:nth-child(9)>div
    left: 146px
    top: 86px
    -webkit-animation-delay: 0.125s
    animation-delay: 0.125s

  .lds-spin>div:nth-child(9)
    -webkit-transform: rotate(360deg)
    transform: rotate(360deg)
    -webkit-transform-origin: 160px 100px
    transform-origin: 160px 100px

  .lds-spin
    width: 26px !important
    height: 26px !important
    -webkit-transform: translate(-13px, -13px) scale(0.13) translate(13px, 13px)
    transform: translate(-13px, -13px) scale(0.13) translate(13px, 13px)

  @keyframes lds-spin
    0%
      opacity: 1
      -webkit-transform: scale(1.1, 1.1)
      transform: scale(1.1, 1.1)

    100%
      opacity: 0
      -webkit-transform: scale(1, 1)
      transform: scale(1, 1)

</style>





