import * as types from './mutation-types'

const mutations = {
  [types.SET_LOADED](state, data) {
    state.isShow = data
  },

  [types.SET_RESTORE](state, data) {
    state.cartGoods = data.localStorageData
  },

  [types.SET_ADDCART](state, data) {
    let findIndex = state.cartGoods.findIndex((e, i) => {
      return e.id === data.id
    })
    if (findIndex === -1) {
      state.cartGoods.push(data)
    } else {
      state.cartGoods[findIndex].num += data.num
      if (state.cartGoods[findIndex].num > 3) {
        state.cartGoods[findIndex].num = 3
      }
    }
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },

  [types.SET_CHECKSINGLE](state, data) {
    state.cartGoods[data].check = !state.cartGoods[data].check
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },

  [types.SET_INCREASEGOOD](state, data) {
    state.cartGoods[data].num++
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },
  [types.SET_DECREASEGOOD](state, data) {
    state.cartGoods[data].num--
    if (state.cartGoods[data].num === 0) {
      state.cartGoods.splice(data, 1)
    }
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },

  [types.SET_DELETEGOOD](state, data) {
    state.cartGoods.splice(data, 1)
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },

  [types.SET_CHECKALL](state, data) {
    state.cartGoods.forEach((e, i) => {
      e.check = !data
    })
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },

  [types.SET_DELETEALLGOODS](state) {
    state.cartGoods = state.cartGoods.filter((e, i) => {
      return !e.check
    })
    localStorage.setItem('goods', JSON.stringify(state.cartGoods))
  },

  [types.SET_WAITINGSTART](state, data) {
    state.isWaiting = data
  },

  [types.SET_WAITINGEND](state, data) {
    state.isWaiting = data
  },

  [types.SET_GWAITINGSTART](state, data) {
    state.isGWaiting = data
  },

  [types.SET_GWAITINGEND](state, data) {
    state.isGWaiting = data
  },

  [types.SET_STOREDETAILDATA](state, data) {
    state.detailData.push(data)
  }
}

export default mutations


