<template>
  <div id="cart">
    <m-header>
      <router-link slot="cart" tag="div" class="back" :to="fromPath">
        <i class="back-button iconfont icon-back"></i>
      </router-link>
      <h2 slot="cart" class="title">
        <span class="text"></span>
      </h2>
      <router-link slot="cart" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
    </m-header>

    <div class="cart-container">
      <ul class="good-list" v-show="cartGoodsData.length">
        <li class="good-item border-tb-1px" v-for="(item,index) in cartGoodsData" :key="index" @click="enterDetail($event, index, item.path)">
          <div class="check" @touchstart="checkSingle(index)" ref="exclude">
            <i class="iconfont icon-checked" :class="item.check ? 'icon-checked-fill' : 'icon-checked'"></i>
          </div>
          <div class="image">
            <img :src="item.image">
          </div>
          <div class="info">
            <p class="title">{{item.title}}</p>
            <p class="type">{{item.name}}</p>
            <p class="price"> {{item.price}}</p>
          </div>
          <div class="delete" ref="exclude">
            <i class="iconfont icon-delete" :class="{active:index === deleteIndex}" @touchstart="deleteGood(index)" @touchend="deleteIndex = -1"></i>
          </div>
          <div class="change" ref="exclude">
            <i class="iconfont icon-decrease decrease" :class="{active:index === decreaseIndex}" @touchstart="decreaseGood(index, item.num)" @touchend="decreaseIndex = -1"></i>
            <span class="number">{{item.num}}</span>
            <i class="iconfont icon-increase increase" :class="{active:index === increaseIndex}" @touchstart="increaseGood(index, item.num)" @touchend="increaseIndex = -1"></i>
          </div>
        </li>
      </ul>

      <div class="cart-empty" v-show="!cartGoodsData.length">
        <p class="text">...</p>
      </div>
      <p style="height:60px;background:#f6f4f8"></p>
    </div>

    <div class="cart-bar">
      <div class="check-all">
        <i class="iconfont" :class="isCheckAll ? 'icon-checked-fill' : 'icon-checked'" @touchstart="checkAll"></i>
        <span class="text"></span>
      </div>
      <div class="total">
        <p class="text">
          <br>
          <span class="num">{{totalPrice}}</span>
        </p>
      </div>
      <div class="delete-all" @touchstart="deleteAll">
        <p class="text"></p>
      </div>
      <div class="settlement">
        <p class="text"></p>
      </div>
    </div>

    <transition name="fade">
      <warn class="warn" title="" v-show="isWarn"></warn>
    </transition>

    <transition name="modal">
      <modal v-show="isModal" @sure="sureSignout" @cancel="cancelSignout" :title="titleMsg"></modal>
    </transition>

    <div class="mask" @touchmove.prevent v-show="isModal"></div>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import Warn from 'base/warn/warn'
import Modal from 'base/modal/modal'

export default {
  data() {
    return {
      titleMsg: '',
      deleteIndex: -1,
      decreaseIndex: -1,
      increaseIndex: -1,
      currentIndex: -1,
      isWarn: false,
      isModal: false,
      fromPath: ''
    }
  },
  created() {
    this._setLocalStorage()
    this.$store.dispatch('gWaitingStart', true)
    setTimeout(() => {
      this.$store.dispatch('gWaitingStart', false)
    }, 500)
  },
  activated() {
    this.$el.nextElementSibling.nextElementSibling.style.display = 'none'
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fromPath = from.path
    })
  },
  beforeRouteLeave(to, from, next) {
    this.isModal = false
    next()
  },
  methods: {
    checkAll() {
      if(this.cartGoodsData.length) {
        this.$store.dispatch('checkAll', this.isCheckAll)
      }
    },
    checkSingle(index) {
      this.$store.dispatch('checkSingle', index)
    },
    deleteGood(index) {
      this.deleteIndex = index
      this.titleMsg = '?'
      this.isModal = true
      this.currentIndex = index
    },
    decreaseGood(index, num) {
      this.currentIndex = index
      this.decreaseIndex = index
      if (num === 1) {
        this.titleMsg = '?'
        this.isModal = true
        return
      }
      this.$store.dispatch('decreaseGood', index)
    },
    increaseGood(index, num) {
      this.currentIndex = index
      this.increaseIndex = index
      if (num === 3) {
        this.isWarn = true
        setTimeout(() => {
          this.isWarn = false
        }, 1000)
        return
      }
      this.$store.dispatch('increaseGood', index)
    },
    sureSignout() {
      this.isModal = false
      if(this.currentIndex !== -1) {
        this.$store.dispatch('deleteGood', this.currentIndex)
        this.currentIndex = -1
      }else {
        this.$store.dispatch('deleteAllGoods')
      }
    },
    cancelSignout() {
      this.isModal = false
    },
    enterDetail(ev, index, path) {
      let excludeArr = this.$refs.exclude.slice(index * 3, index * 3 + 3)
      if (!excludeArr.includes(ev.target.parentNode) && !excludeArr.includes(ev.target)) {
        this.$router.push(path)
      }
    },
    deleteAll() {
      let isCanDeleteAll = this.cartGoodsData.some((e, i) => {
        return e.check
      })
      if (isCanDeleteAll) {
        this.titleMsg = '?'
        this.isModal = true
      }
    },
    _setLocalStorage() {
      let localStorageData = JSON.parse(localStorage.getItem('goods')) || []
      this.$store.dispatch('restore', {
        localStorageData
      })
    }
  },
  computed: {
    cartGoodsData() {
      return this.$store.state.cartGoods
    },
    totalPrice() {
      if (this.$store.getters.totalPrice) {
        return this.$store.getters.totalPrice
      } else {
        return 0.00
      }
    },
    isCheckAll() {
      return this.$store.getters.isCheckAll
    }
  },
  components: {
    MHeader,
    Warn,
    Modal
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  #cart
    .m-header
      position: fixed
      width: 100%
      line-height: 50px
      text-align: center
      border-bottom: 1px solid #f6f4f8
      font-size: 0
      z-index: 10000
      .back
        display: inline-block
        float: left
        width: 50px
        height: 50px
        text-align: center
        .back-button
          font-size: $font-size-large-x
      .title
        display: inline-block
        height: 50px
        .text
          font-size: 20px
          color: #3c363e
      .home
        display: inline-block
        float: right
        width: 50px
        height: 50px
        text-align: center
        .icon-home
          font-size: $font-size-large-x

    .cart-container
      background: #fff
      overflow: scroll
      position: relative
      left: 0
      top: 50px
      .good-list
        .good-item
          position: relative
          overflow: hidden
          padding: 20px
          border-top-1px(#f6f4f8)
          .check
            float: left
            width: 50px
            height: 50px
            line-height: 50px
            text-align: center
            margin-right: 10px
            i
              font-size: 26px
            .icon-checked-fill
              color: #f55669
          .image
            float: left
            margin-right: 14px
            img
              display: block
              width: 80px
          .info
            float: left
            .title
              height: 30px
              width: 160px
              line-height: 30px
              overflow: hidden
              text-overflow: ellipsis
              white-space: nowrap
              color: #2a242b
            .type
              height: 30px
              line-height: 30px
              font-size: $font-size-medium
              color: #7e8c8d
            .price
              height: 30px
              line-height: 30px
              font-size: $font-size-medium
              color: #f55669
          .delete
            position: absolute
            right: 0
            top: 0
            width: 50px
            height: 50px
            line-height: 50px
            text-align: center
            i
              font-size: 24px
              color: #a4a4a4
              &.active
                color: #f55669
          .change
            position: absolute
            right: 20px
            bottom: 20px
            font-size: 0
            .decrease
              display: inline-block
              vertical-align: middle
              width: 50px
              height: 50px
              line-height: 50px
              text-align: center
              font-size: 26px
              color: #a4a4a4
              &.active
                color: #f55669
            .number
              display: inline-block
              vertical-align: middle
              width: 10px
              height: 50px
              line-height: 50px
              text-align: center
              font-size: 18px
              color: #2a242b
            .increase
              display: inline-block
              vertical-align: middle
              width: 50px
              height: 50px
              line-height: 50px
              text-align: center
              font-size: 26px
              color: #a4a4a4
              &.active
                color: #f55669
          
      .cart-empty
        .text
          padding: 100px 10px
          text-align: center
          color: #3c363e
          font-size: $font-size-medium
      
    .cart-bar
      display: flex
      position: fixed
      left: 0
      bottom: 0
      width: 100%
      height: 60px
      background: #fff
      .check-all
        flex: 1
        text-align: center
        i
          display: inline-block
          margin: 8px 0 2px
          font-size: 26px
        .icon-checked-fill
            color: #f55669
        .text
          display: block
          height: 20px
          line-height: 20px
          color: #3c363e
      .total
        flex: 1
        position: relative
        .text
          position: absolute
          left: 0
          bottom: 0
          line-height: 22px
          color: #f55669
          font-size: 15px
      .delete-all
        flex: 1
        line-height: 60px
        text-align: center
        background: #f55669
        .text
          color: #fff
          font-size: $font-size-large
      .settlement
        flex: 1
        line-height: 60px
        text-align: center
        background: #efb336
        .text
          color: #fff
          font-size: $font-size-large
    
    .warn
      position: fixed

    .mask
      position: absolute
      left: 0
      top: 0
      width: 100%
      height: 100%
      background: rgba(0, 0, 0, 0.5)

  .fade-enter-active, .fade-leave-active
    transition: opacity 1.1s

  .fade-enter, .fade-leave-to
    opacity: 0

  .modal-enter-active, .modal-leave-active
    transition: opacity 0.5s

  .modal-enter, .modal-leave-to
    opacity: 0

</style>


