<template>
  <div class="phone">
    <m-header>
      <router-link slot="phone" tag="div" class="back" to="/">
        <i class="back-button iconfont icon-back"></i>
      </router-link>
      <h2 slot="phone" class="title">
        <span class="text"></span>
      </h2>
      <router-link slot="phone" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
    </m-header>

    <phone-list :PhoneListImagesData="PhoneListImages"></phone-list>
  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import PhoneList from 'components/phone/phone-list'
import { getPhoneData } from 'api'

export default {
  data() {
    return {
      PhoneListImages: []
    }
  },
  created() {
    this._getPhoneData()
  },
  methods: {
    _getPhoneData() {
      this.$store.dispatch('gWaitingStart', true)
      getPhoneData().then((res) => {
        if(res && res.status === 'ok') {
          this.$store.dispatch('gWaitingEnd', false)
          this.PhoneListImages = res.result.list
        }
      })
    }
  },
  components: {
    MHeader,
    PhoneList
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"

  .m-header
    line-height: 50px
    text-align: center
    font-size: 0
    .back
      display: inline-block
      float: left
      width: 50px
      height: 50px
      text-align: center
      .back-button
        font-size: $font-size-large-x
    .title
      display: inline-block
      height: 50px
      .text
        font-size: 20px
        color: #3c363e
    .home
      display: inline-block
      float: right
      width: 50px
      height: 50px
      text-align: center
      .icon-home
        font-size: $font-size-large-x

</style>





