<template>
  <div id="g-waiting" @touchmove.prevent>
    <div class="g-waiting-content">
      <div style="width:100%;height:100%" class="lds-ellipsis">
        <div>
          <div></div>
        </div>
        <div>
          <div></div>
        </div>
        <div>
          <div></div>
        </div>
        <div>
          <div></div>
        </div>
        <div>
          <div></div>
        </div>
      </div>
      <div class="g-waiting-text">loading...</div>
    </div>
  </div>
</template>

<script>
export default {
  
}
</script>

<style lang="stylus" scoped>
  #g-waiting
    width: 100%
    height: 100%
    position: fixed
    left: 0
    top: 50px
    display: table
    z-index 140
    .g-waiting-content
      display: table-cell
      vertical-align: middle
      background: #fff

  .g-waiting-text
    text-indent: 6px
    text-align: center
    position: relative
    top: -80px
    font-size: 14px
    color: #333

  .lds-ellipsis
    position: relative
    margin: 0 auto
    top: -80px

  .lds-ellipsis>div
    position: absolute
    -webkit-transform: translate(-50%, -50%)
    transform: translate(-50%, -50%)
    width: 40px
    height: 40px

  .lds-ellipsis div>div
    width: 34px
    height: 34px
    border-radius: 50%
    background: #f00
    position: absolute
    top: 100px
    left: 32px
    -webkit-animation: lds-ellipsis 2s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards
    animation: lds-ellipsis 2s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards

  .lds-ellipsis div:nth-child(1) div
    -webkit-animation: lds-ellipsis2 2s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards
    animation: lds-ellipsis2 2s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards
    background: #ff6d00

  .lds-ellipsis div:nth-child(2) div
    -webkit-animation-delay: -1s
    animation-delay: -1s
    background: #ff003a

  .lds-ellipsis div:nth-child(3) div
    -webkit-animation-delay: -0.5s
    animation-delay: -0.5s
    background: #e7008a

  .lds-ellipsis div:nth-child(4) div
    -webkit-animation-delay: 0s
    animation-delay: 0s
    background: #4658ac

  .lds-ellipsis div:nth-child(5) div
    -webkit-animation: lds-ellipsis3 2s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards
    animation: lds-ellipsis3 2s cubic-bezier(0, 0.5, 0.5, 1) infinite forwards
    background: #ff6d00

  .lds-ellipsis
    width: 50px !important
    height: 30px !important
    -webkit-transform: translate(-25px, -25px) scale(0.25) translate(25px, 25px)
    transform: translate(-25px, -25px) scale(0.25) translate(25px, 25px)

  @keyframes lds-ellipsis3
    0%,
    25%
      left: 32px
      -webkit-transform: scale(0)
      transform: scale(0)

    50%
      left: 32px
      -webkit-transform: scale(1)
      transform: scale(1)

    75%
      left: 100px

    100%
      left: 168px
      -webkit-transform: scale(1)
      transform: scale(1)

  @keyframes lds-ellipsis2
    0%
      -webkit-transform: scale(1)
      transform: scale(1)

    25%,
    100%
      -webkit-transform: scale(0)
      transform: scale(0)

  @keyframes lds-ellipsis
    0%
      left: 32px;
      -webkit-transform: scale(0)
      transform: scale(0)

    25%
      left: 32px
      -webkit-transform: scale(1)
      transform: scale(1)

    50%
      left: 100px

    75%
      left: 168px
      -webkit-transform: scale(1)
      transform: scale(1)

    100%
      left: 168px
      -webkit-transform: scale(0)
      transform: scale(0)

</style>





