<template>
  <div class="goods">
    <div class="goods-recommend">
      <div class="recommend-item" v-for="item in goodImagesData.recommend" :key="item.id">
        <router-link class="recommend-link" :to="item.href.slice(26, -5)">
          <img class="recommend-images" v-lazy="item.image">
        </router-link>
      </div>
    </div>

    <div class="goods-list">
      <div class="list-item border-tb-1px" v-for="item in goodImagesData.list" :key="item.id">
        <router-link class="list-link border-lr-1px" :to="item.href.slice(26, -5)">
          <img class="list-images" v-lazy="item.image">
          <h3 class="name">{{item.name}}</h3>
          <p class="info">{{item.description}}</p>
          <p class="price">{{item.price}}</p>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    'goodImagesData': Object
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .goods
    background: #fff
    .goods-recommend
      overflow: hidden
      .recommend-item
        float: left
        width: 50%
        .recommend-link
          display: block
          width: 100%
          .recommend-images
            display: block
            width: 100%

    .goods-list
      overflow: hidden
      .list-item
        float: left
        width: 50%
        box-sizing border-box
        border-bottom-1px(#f6f4f8)
        &:nth-last-child(1), &:nth-last-child(2)
          &:after
            border-top: 0
        &:nth-child(2n+1)
          .list-link
            border-right-1px(#f6f4f8)
        .list-link
          display: block
          padding: 24px 0
          .list-images
            display: block
            width: 106px
            margin: 0 auto 6px
          .name
            height: 24px
            line-height: 24px
            text-align: center
            overflow: hidden
            font-size: $font-size-medium
            color: #29232a
          .info
            min-height 24px
            line-height: 24px
            text-align: center
            font-size: $font-size-medium
            color: #999
          .price
            height: 24px
            line-height: 24px
            text-align: center
            font-size: $font-size-medium
            color: #f55669
            
</style>





