import * as types from './mutation-types'

export const loaded = ({ commit }, data) => {
  commit(types.SET_LOADED, data)
}

export const restore = ({ commit }, data) => {
  commit(types.SET_RESTORE, data)
}

export const addCart = ({ commit }, data) => {
  commit(types.SET_ADDCART, data)
}

export const checkSingle = ({ commit }, data) => {
  commit(types.SET_CHECKSINGLE, data)
}

export const increaseGood = ({ commit }, data) => {
  commit(types.SET_INCREASEGOOD, data)
}

export const decreaseGood = ({ commit }, data) => {
  commit(types.SET_DECREASEGOOD, data)
}

export const deleteGood = ({ commit }, data) => {
  commit(types.SET_DELETEGOOD, data)
}

export const checkAll = ({ commit }, data) => {
  commit(types.SET_CHECKALL, data)
}

export const deleteAllGoods = ({ commit }) => {
  commit(types.SET_DELETEALLGOODS)
}

export const waitingStart = ({ commit }, data) => {
  commit(types.SET_WAITINGSTART, data)
}

export const waitingEnd = ({ commit }, data) => {
  commit(types.SET_WAITINGEND, data)
}

export const gWaitingStart = ({ commit }, data) => {
  commit(types.SET_GWAITINGSTART, data)
}

export const gWaitingEnd = ({ commit }, data) => {
  commit(types.SET_GWAITINGEND, data)
}

export const storeDetailData = ({ commit }, data) => {
  commit(types.SET_STOREDETAILDATA, data)
}


