<template>
  <div class="home">
    <loading v-show="isShow"></loading>

    <notice :isShow="isShowNotice" @show="showNotice" @hidden="hiddenNotice" :noticeData="notice"></notice>

    <m-header>
      <h1 slot="home" class="logo"></h1>

      <div slot="home" class="cart">
        <router-link to="/cart">
          <i class="cart-button iconfont icon-cart"></i>
          <em class="cart-count">{{carNum}}</em>
        </router-link>
      </div>

      <div slot="home" class="user">
        <router-link to="/user">
          <i class="user-button iconfont icon-user"></i>
        </router-link>
      </div>

      <div slot="home" class="search" @touchstart="showSearch">
        <i class="search-button iconfont icon-search"></i>
      </div>
    </m-header>

    <transition name="fade">
      <m-search :categoriesData="categories" v-show="isShowSearch" @cancelSearch="cancelSearch"></m-search>
    </transition>
	
    <div class="slider-wrapper" ref="sliderWrapper">
      <slider v-if="sliderImages.length">
        <div v-for="item in sliderImages" :key="item.id">
          <a :href="item.href">
            <img :src="item.image">
          </a>
        </div>
      </slider>
    </div>

    <tab></tab>

    <m-title>
      <span class="m-title-name"></span>
    </m-title>

    <div class="recommend-wrapper" ref="recommendWrapper">
      <recommend v-if="recommendImages.length">
        <div v-for="item in recommendImages" :key="item.id">
          <a :href="item.href">
            <img :src="item.image">
          </a>
        </div>
      </recommend>
    </div>

    <m-title>
      <span class="m-title-name"></span>
    </m-title>

    <div class="phone-wrap">
      <phone :PhoneImagesData="PhoneImages"></phone>
    </div>

    <m-title>
      <span class="m-title-name"></span>
      <router-link to="/search/general" tag="span" class="m-title-more"></router-link>
    </m-title>

    <div class="goods-wrap">
      <goods :goodImagesData="goodImages"></goods>
    </div>

    <m-title>
      <span class="m-title-name">MeituFamily</span>
      <router-link to="/search/life" tag="span" class="m-title-more"></router-link>
    </m-title>

    <div class="goods-wrap">
      <goods :goodImagesData="meituFamilyImages"></goods>
    </div>

    <div class="mask-search" @touchmove.prevent v-show="isShowSearch"></div>

    <div class="mask-notice" @touchmove.prevent v-show="isShowNotice"></div>
  </div>
</template>

<script>
import Loading from 'base/loading/loading'
import MHeader from 'base/m-header/m-header'
import Slider from 'base/slider/slider'
import Tab from 'components/home/tab/tab'
import MTitle from 'components/home/m-title'
import Recommend from 'components/home/recommend'
import Phone from 'components/home/phone'
import Goods from 'components/home/goods'
import MSearch from 'base/m-search/m-search'
import Notice from 'base/notice/notice'
import { getHomeData } from 'api'
import { mapGetters } from 'vuex'

export default {
  data() {
    return {
      sliderImages: [],
      recommendImages: [],
      PhoneImages: [],
      goodImages: {},
      meituFamilyImages: {},
      categories: [],
      isShowSearch: false,
      searchSelect: [],
      isShowNotice: false,
      notice: {}
    }
  },
  created() {
    this._getHomeData()
    this._setLocalStorage()
  },
  activated() {
    this.$el.nextElementSibling.nextElementSibling.style.display = 'block'
  },
  deactivated() {
    this.isShowSearch = false
  },
  methods: {
    cancelSearch() {
      this.isShowSearch = !this.isShowSearch
    },
    showSearch() {
      this.isShowSearch = !this.isShowSearch
    },
    showNotice() {
      this.isShowNotice = true
    },
    hiddenNotice() {
      this.isShowNotice = false
    },
    _getHomeData() {
      getHomeData().then((res) => {
        if (res.status === 'ok') {
          this.$store.dispatch('loaded', false)
          this.sliderImages = res.result.banner_data
          this.recommendImages = res.result.recommend
          this.PhoneImages = res.result.hardware.list
          this.goodImages = res.result.accessories
          this.meituFamilyImages = res.result.life
          this.categories = res.result.categories
          this.notice = res.result.notice
          for (let i = 0; i < this.categories.length; i++) {
            if (i === 0 || i === this.categories.length - 1) continue
            this.searchSelect.push(this.categories[i].name)
          }
        }
      })
    },
    _setLocalStorage() {
      let localStorageData = JSON.parse(localStorage.getItem('goods')) || []
      this.$store.dispatch('restore', {
        localStorageData
      })
    }
  },
  computed: {
    /*...mapGetters([
      'isShow'
    ]),*/
    isShow() {
      return this.$store.getters.isShow
    },
    carNum() {
      return this.$store.getters.cartTotal
    }
  },
  components: {
    Loading,
    MHeader,
    Slider,
    Tab,
    MTitle,
    Recommend,
    Phone,
    Goods,
    MSearch,
    Notice
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/mixin"
  @import "~common/stylus/variable"

  .m-header
    position: relative
    z-index: 3
    font-size: 0
    .logo
      float: left
      display: inline-block
      vertical-align: top
      margin: 14px 0 0 14px
      width: 144px
      height: 22px
      bg-image('logo')
      background-size: 144px 22px
      background-repeat no-repeat
    .search
      float: right
      display: inline-block
      vertical-align: top
      width: 50px
      height: 50px
      line-height: 50px
      text-align: center
      .search-button
        font-size: $font-size-large-x
    .user
      float: right
      display: inline-block
      vertical-align: top
      width: 50px
      height: 50px
      line-height: 50px
      text-align: center
      .user-button
        font-size: $font-size-large-x
    .cart
      position: relative
      float: right
      display: inline-block
      vertical-align: top
      width: 50px
      height: 50px
      line-height: 50px
      text-align: center
      .cart-button
        font-size: $font-size-large-x
      .cart-count
        position: absolute
        right: 0
        top: 2px
        display: block
        width: 18px
        height: 18px
        line-height: 18px
        text-align: center
        border-radius: 50%
        font-size: 14px
        font-style: normal
        color: #fff
        background: #f55669

  .slider-wrapper
    min-height: 150px
    bg-image('bg')
    background-size 85px 85px
    background-repeat no-repeat
    background-position center center
    
  .m-title-more
    float: right

  .recommend-wrapper
    min-height: 150px
    bg-image('bg')
    background-size 85px 85px
    background-repeat no-repeat
    background-position center center

  .phone-wrap
    min-height: 150px
    bg-image('bg')
    background-size 85px 85px
    background-repeat no-repeat
    background-position center center

  .goods-wrap
    min-height: 150px
    bg-image('bg')
    background-size 85px 85px
    background-repeat no-repeat
    background-position center center

  .mask-search
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.5)

  .mask-notice
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.5)
    z-index: 10

  .fade-enter-active, .fade-leave-active // 
    transition: all 0.5s
  .fade-enter, .fade-leave-to // 
    opacity: 0
    transform: translateY(-380px)
  
</style>





