<template>
  <div class="login">

    <m-header>
      <span class="login-title" slot="login"></span>
      <span class="line-colorful" slot="login"></span>
    </m-header>

    <div class="login-container">
      <div class="container-inner">
        <ul class="formlist">
          <li class="item">
            <input type="tel" autocomplete="off" placeholder="13026321002" class="username" v-model="phone">
          </li>
          <li class="item">
            <input type="password" placeholder="123456" class="password" v-model="password">
          </li>
        </ul>
        <p class="login-button" @touchstart="login"></p>
        <div class="login-more">
          <router-link to="/" class="forget"></router-link>
          <router-link to="/" class="register"></router-link>
        </div>
        <p class="line-separate"></p>
        <p class="login-other">
          <router-link to="/"></router-link>
        </p>
        <div class="login-icons">
          <div class="icon-item">
            <router-link to="/" class="weibo"></router-link>
            <span class="text"></span>
          </div>
          <div class="icon-item">
            <router-link to="/" class="qq"></router-link>
            <span class="text">QQ</span>
          </div>
        </div>
      </div>
    </div>

    <transition name="fade">
      <warn :title="warnInfo" v-show="isWarn" ref="warn"></warn>
    </transition>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import Warn from 'base/warn/warn'

export default {
  data() {
    return {
      phone: '',
      password: '',
      warnInfo: '',
      isWarn: false
    }
  },
  created() {
    this.$store.dispatch('gWaitingStart', true)
    setTimeout(() => {
      this.$store.dispatch('gWaitingStart', false)
    }, 500)
  },
  mounted() {
    this._isLogined()
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm._isLogined()
    })
  },
  methods: {
    login() {
      if (this.phone === '') {
        this._warnShow('')
        this._warnHide()
        return
      }
      if (!/^\d{11}$/.test(this.phone)) {
        this._warnShow('')
        this._warnHide()
        return
      }
      if (this.password === '') {
        this._warnShow('')
        this._warnHide()
        return
      }

      let re = /^1(3|4|5|7|8)[0-9]\d{8}$/
      if (re.test(this.phone)) {
        let serverUser = this.$store.getters.user
        if (this.phone !== serverUser.phone) {
          this._warnShow('')
          this._warnHide()
          return
        }
        if (this.password !== serverUser.password) {
          this._warnShow('')
          this._warnHide()
          return
        }
        let loginUser = {
          phone: this.phone,
          password: this.password
        }
        localStorage.setItem('user', JSON.stringify(loginUser))
        let redirect = decodeURIComponent(this.$route.query.redirect || '/user')
        this.$router.push({
          path: redirect
        })
        this.phone = this.password = ''
        this.$store.dispatch('gWaitingStart', true)
        setTimeout(() => {
          this.$store.dispatch('gWaitingStart', false)
        }, 500)
      } else {
        this._warnShow('')
        this._warnHide()
      }
    },
    _isLogined() {
      let redirect = decodeURIComponent(this.$route.query.redirect || '/')
      this.$router.push({
        path: redirect
      })
    },
    _warnShow(info) {
      this.warnInfo = info
      this.isWarn = true
    },
    _warnHide() {
      setTimeout(() => {
        this.isWarn = false
      }, 1000)
    }
  },
  components: {
    MHeader,
    Warn
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/mixin"
  @import "~common/stylus/variable"

  .login
    position: relative
    .m-header
      position: relative
      height: 60px
      line-height: 60px
      text-align: center
      .login-title
        font-size: $font-size-large-x
        color: #737373
      .line-colorful
        position: absolute
        left: 0
        bottom: 0
        width: 100%
        height: 2px
        background: linear-gradient(90deg,red 16.7%,#ed0b13 20%,#c02a46 22.5%,#745d9a 25.8%,#6369af 27.1%,#5f6db7 27.7%,#6b6bbc 28.9%,#8661b5 29.7%,#b7499b 30.8%,#c34594 31.1%,#d2428d 32.1%,#d9627f 35.1%,#e36a5e 36%,#f86e19 37.4%,#f90 40.7%,#ff9e00 41.6%,#fe9901 42.4%,#f87607 43.7%,#e6471b 44.8%,#de3d24 45.1%,#d13332 45.6%,#c43142 46%,#ba334c 46.3%,#a23e68 47.3%,#6f61a0 49.6%,#5f6db7 51.2%,#6b6bbc 52.5%,#8661b5 53.2%,#b7499b 54.3%,#c34594 54.7%,#d2428d 55.6%,#d9627f 58.6%,#e36a5e 59.5%,#f86e19 60.9%,#f90 64.2%,#ff9d00 65.2%,#ff9b00 65.6%,#ff9200 66.3%,#ff8a00 66.6%,#ff6a00 67.3%,#ff2f00 68.4%,#ff0d00 69.4%,red)

    .login-container
      padding: 40px 20px 20px
      .container-inner
        .formlist
          margin-bottom: 20px
          border 1px solid #d5d5d5
          border-radius: 6px
          background: #fff
          .item
            input
              width: 100%
              padding: 16px
              box-sizing: border-box
              background: none
            &:first-child
              border-bottom 1px solid #d5d5d5

        .login-button
          margin-bottom: 20px
          height: 50px
          line-height: 50px
          text-align: center
          background: #fc3c60
          border-radius: 6px
          color: #fff
          font-size: $font-size-large

        .login-more
          overflow: hidden
          margin-bottom: 15px
          a
            font-size: 15px
            text-decoration: underline
            color: #737373
          .forget
            float: left
          .register
            float: right
        
        .line-separate
          border-top: 1px solid #dbdbdb
          margin-bottom: 15px

        .login-other
          margin-bottom: 20px
          a
            font-size: 15px
            color: #737373

        .login-icons
          .icon-item
            display: inline-block
            a
              display: block
              width: 50px
              height: 50px
              margin: 0 auto 4px
              background-size: 30px 30px
              background-repeat: no-repeat
              background-position: 10px 10px
              border-radius: 50%
            .weibo
              bg-image('weibo')
              background-color: #ea3133
            .qq
              bg-image('qq')
              background-color: #1578e8
            .text
              font-size: 15px
              color: #737373
            &:first-child
              margin-right: 30px

  .fade-enter-active, .fade-leave-active
    transition: opacity 0.5s

  .fade-enter, .fade-leave-to
    opacity: 0

</style>




