<template>
  <div class="phone-list border-tb-1px">
    <div class="phone-item border-tb-1px" v-for="item in PhoneListImagesData" :key="item.id">
      <a :href="item.href" class="phone-link">
        <img class="phone-images" v-lazy="item.image">
        <div class="phone-text">
          <h3 class="name">{{item.name}}</h3>
          <p class="info">{{item.description}}</p>
          <p class="price">{{item.price}}</p>
        </div>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    'PhoneListImagesData': Array
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .phone-list
    background: #fff
    border-top-1px(#f6f4f8)
    .phone-item
      border-bottom-1px(#f6f4f8)
      &:last-child
        border-bottom 0
      .phone-link
        display: block
        overflow: hidden
        padding: 20px 20px 0
        .phone-images
          display: block
          float: left
          width: 130px
        .phone-text
          float: right
          margin-top: 20px
          width: 150px
          .name
            margin-bottom: 10px
            // text-align: center
            color: #29232a
            font-size: 16px
          .info
            margin-bottom: 10px
            // text-align: center
            color: #999
            font-size: 15px
          .price
            // text-align: center
            color: #f55669
            font-size: 14px

</style>




