<template>
  <div id="notice">
    <h3 class="notice-brief">
      <div class="notice-roll-wrap">
        <p class="notice-roll">{{noticeData.brief}}</p>
      </div>
      <span class="notice-show" @touchstart="show"></span>
    </h3>
    <div class="notice-content" v-show="isShow" @touchmove.prevent>
      <div class="notice-close" @touchstart="hidden">
        <i class="close-button iconfont icon-close"></i>
      </div>
      <h4 class="notice-title">{{noticeData.title}}</h4>
      <p class="notice-text" v-html="noticeData.detail"></p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    isShow: Boolean,
    noticeData: Object
  },
  methods: {
    show() {
      this.$emit('show')
    },
    hidden() {
      this.$emit('hidden')
    }
  }
}
</script>

<style lang="stylus" scoped>

  #notice
    .notice-brief
      position: relative
      height: 30px
      line-height: 30px
      text-align: center
      background: #555
      z-index: 3
      .notice-roll-wrap
        position: relative
        display: inline-block
        vertical-align: top
        width: 75%
        height: 100%
        overflow: hidden
        .notice-roll
          position: absolute
          left: 0
          top: 0
          white-space: nowrap
          height: 100%
          animation: infiniteMove 20s linear 2s infinite
          font-size: 15px
          color: #b5b5b5
      .notice-show
        display: inline-block
        vertical-align: top
        height: 30px
        font-size: 14px
        color: #efb336
    .notice-content
      position: absolute
      left: 0
      top: 0
      padding: 50px 20px 20px
      width: 90%
      box-sizing: border-box
      margin: 100px 5%
      border-radius: 4px
      z-index: 100
      background: #fff
      .notice-close
        position: absolute
        right: 0
        top: 0
        width: 50px
        height: 50px
        line-height: 50px
        text-align: center
        .close-button
          font-size: 18px
      .notice-title
        line-height: 24px
        font-size: 15px
        font-weight: bold
        color: #f55669
      .notice-text
        line-height: 24px
        font-size: 14px
        color: #666

  @keyframes infiniteMove
    from
      transform: translateX(0)
    
    to
      transform: translateX(-100%)

</style>



