<template>
  <div class="warn">
    <span class="title">{{title}}</span>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    }
  }
}
</script>

<style lang="stylus" scoped>
  .warn
    position: absolute
    left: 0
    right: 0
    top: -160px
    bottom: 0
    margin: auto
    width: 200px
    height: 40px
    line-height: 40px
    text-align: center
    border-radius: 20px
    background: rgba(0, 0, 0, 0.4)
    z-index: 10000
    .title
      color: #fff

</style>






