<template>
  <div class="tab">
  
    <router-link tag="div" class="tab-item" to="/phone">
      <span class="icon icon-phone">
        <i class="phone"></i>
      </span>
      <span class="text"></span>
    </router-link>
  
    <router-link tag="div" class="tab-item" to="/goods">
      <span class="icon icon-goods">
        <i class="goods"></i>
      </span>
      <span class="text"></span>
    </router-link>
  
    <router-link tag="div" class="tab-item" to="/services">
      <span class="icon icon-services">
        <i class="services"></i>
      </span>
      <span class="text"></span>
    </router-link>
  
    <router-link tag="div" class="tab-item" to="/shop">
      <span class="icon icon-shop">
        <i class="shop"></i>
      </span>
      <span class="text"></span>
    </router-link>
  
  </div>
</template>

<script>
export default {

}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/mixin"

  .tab
    display: flex
    background: #fff
    .tab-item
      flex 1
      width: 108px
      height: 116px
      .icon
        position: relative
        display: block
        width: 60px
        height: 60px
        text-align: center
        margin: 18px auto 8px
        border-radius 50%
        i
          position: absolute
          display: block
          left: 0
          right: 0
          top: 0
          bottom: 0
          margin auto
          background-repeat no-repeat
        .phone
          width: 14px
          height: 29px
          bg-image('phone')
          background-size 14px 29px
        .goods
          width: 24px
          height: 24px
          bg-image('goods')
          background-size 24px 24px
        .services
          width: 20px
          height: 18px
          bg-image('services')
          background-size 20px 18px
        .shop
          width: 20px
          height: 20px
          bg-image('shop')
          background-size 20px 20px
      .icon-phone
        background-color: #46c0ff
      .icon-goods
        background-color: #94d00b
      .icon-services
        background-color: #ff9c7d
      .icon-shop
        background-color: #ff71aa

      .text
        display: block
        text-align: center
        color: #5c5c5c
        font-size: 15px

</style>





