<template>
  <div class="m-search" @touchmove.prevent>

    <div class="search-wrap border-tb-1px">
      <router-link tag="div" class="search-button" to="/">
        <i class="search-icon iconfont icon-search"></i>
      </router-link>
      <input type="text" class="search-box" :placeholder="categoriesData.length ? categoriesData[0].children[0].name : ''">
      <span class="search-cancel" @touchstart="cancelSearch"></span>
    </div>

    <div class="subcate" v-for="(items,index) in categoriesData" :key="items.id">
      <p class="subcate-title">
        <router-link :to="items.href.substr(20)">{{items.name}}</router-link>
      </p>
      <ul class="subcate-list" v-if="index === 0">
        <li class="subcate-item" v-for="item in items.children" :key="item.id">
          <a :href="item.href">{{item.name}}</a>
        </li>
      </ul>
    </div>

    <div class="search-mask"></div>
  
  </div>
</template>

<script>

export default {
  props: {
    categoriesData: Array
  },
  methods: {
    cancelSearch() {
      this.$emit('cancelSearch')
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .m-search
    position: absolute
    width: 100%
    z-index: 2
    background: #fff

    .search-wrap
      display: flex
      align-items: center
      height: 50px
      line-height: 50px
      position: relative
      padding: 0 0 0 14px
      border-top-1px(#ddd)
      .search-button
        position: absolute
        left: 16px
        top: 10px
        width: 30px
        height: 30px
        line-height: 32px
        text-align: center
        .search-icon
          font-size: $font-size-large
          color: #a4a4a4
      .search-box
        flex: 1
        height: 30px
        box-sizing: border-box
        text-indent: 30px
        border: 1px solid #cdced1
        border-radius: 4px
        color: #7e8c8d
        font-size: $font-size-medium
      .search-cancel
        width: 50px
        height: 30px
        line-height: 32px
        text-align: center
        color: #7e8c8d

    .subcate
      margin: 0 20px
      padding: 10px 0
      .subcate-title
        height: 30px
        a
          display: block
          line-height: 30px
          width: 200px
          color: #222
      .subcate-list
        overflow: hidden
        color: #5c5c5c
        font-size: 15px
        .subcate-item
          float: left
          width: 50%
          line: 40px
          a
            display: block
            line-height: 40px
            color: #5c5c5c

</style>




