<template>
  <div class="list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="0" infinite-scroll-immediate-check="true">
    <div class="list-item border-tb-1px" v-for="item in GoodsListImagesData" :key="item.id">
      <router-link :to="`/detail/${item.product_id}`" class="list-link border-lr-1px">
        <img v-lazy="item.image" class="list-image">
        <h3 class="name">{{item.product_name}}</h3>
        <p class="price">{{item.price}}</p>
      </router-link>
    </div>
  </div>
</template>

<script>
import infiniteScroll from 'vue-infinite-scroll'

export default {
  props: {
    'GoodsListImagesData': Array
  },
  data() {
    return {
      loading: false
    }
  },
  directives: { infiniteScroll },
  methods: {
    loadMore() {
      this.$parent.$el.nextElementSibling.nextElementSibling.style.display = 'none'
      this.loading = true
      this.$emit('sendLoading', this.loading)
      setTimeout(() => {
        this.loading = false
      }, 1000)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .list
    overflow: hidden
    background: #fff
    .list-item
      float: left
      width: 50%
      box-sizing border-box
      border-bottom-1px(#f6f4f8)
      &:nth-last-child(1), &:nth-last-child(2)
          &:after
            border-top: 0
        &:nth-child(2n+1)
          .list-link
            border-right-1px(#f6f4f8)
      .list-link
        display: block
        padding:24px 0
        .list-image
          display: block
          width: 106px
          margin: 0 auto 6px
        .name
          height: 24px
          line-height: 24px
          text-align: center
          overflow: hidden
          font-size: $font-size-medium
          color: #29232a
        .price
          height: 24px
          line-height: 24px
          text-align: center
          font-size: $font-size-medium
          color: #f55669
      
</style>




