<template>
  <div class="shop">

    <m-header>
      <router-link tag="h1" to="/" slot="shop" class="logo"></router-link>

      <div slot="shop" class="user">
        <router-link to="/user">
          <i class="user-button iconfont icon-user"></i>
        </router-link>
      </div>
    </m-header>

    <div class="banner">
      <img class="image" src="./banner.jpg">
    </div>

    <div class="shop-select">
      <div class="province">
        <div class="province-show border-lr-1px">
          <span class="text">{{currentProvince}}</span>
          <i class="iconfont icon-select select-btn"></i>
        </div>
        <select class="province-select" v-model="currentSelectProvince" ref="provinceSelect">
          <option :value="item.id" v-for="item in provinceList" :key="item.id">{{item.name}}</option>
        </select>
      </div>

      <div class="city">
        <div class="city-show">
          <span class="text">{{currentCity}}</span>
          <i class="iconfont icon-select select-btn"></i>
        </div>
        <select class="city-select" v-model="currentSelectCity" ref="citySelect">
          <option :value="item.id" v-for="item in cityList" :key="item.id">{{item.name}}</option>
        </select>
      </div>
    </div>

    <div class="shop-show" v-if="shopDatail.length">
      <div class="shop-item" v-for="item in shopDatail" :key="item.id">
        <router-link to="/" class="shop-detail border-tb-1px">
          <p class="shop-name">{{item.name}}</p>
          <p class="shop-business-hours">{{item.office_hours}}</p>
          <p class="shop-phone">{{item.phone}}</p>
          <p class="shop-address">{{item.address}}</p>
        </router-link>
        <div class="shop-bottom">
          <a href="javascript:;" class="shop-map border-lr-1px">
            <i class="iconfont icon-map"></i>
            <span class="text"></span>
          </a>
          <a :href="'tel:' + item.phone" class="shop-phone">
            <i class="iconfont icon-tel"></i>
            <span class="text"></span>
          </a>
        </div>
      </div>
    </div>
    <div class="shop-noopend" v-show="isShow">
      <p class="text"></p>
    </div>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import { getProvinceData, getCityData, getLocationData, getShopData } from 'api'

export default {
  data() {
    return {
      currentProvince: '',
      currentSelectProvince: '',
      provinceList: [],
      provinceId: 0,
      currentCity: '',
      currentSelectCity: 1,
      cityList: [],
      shopDatail: [],
      isShow: false
    }
  },
  created() {
    this._getProvinceData()
  },
  methods: {
    _getProvinceData() {
      this.$store.dispatch('waitingStart', true)
      getProvinceData().then((res) => {
        let data = res.result
        data.forEach((e, i) => {
          e.provinces.forEach((e, i) => {
            this.provinceList.push({
              id: e.id,
              name: e.name
            })
          })
        })
        this.provinceList.unshift({
          name: ''
        })
        this._getLocationData()
      })
    },
    _getLocationData() {
      getLocationData().then((res) => {
        this.provinceId = res.data.area_code.substr(0, 2)
        this._getCityData(this.provinceId)
      })
    },
    _getCityData(provinceId) {
      getCityData(provinceId).then((res) => {
        this.cityList = res.data.result[0].result
        this.cityList.unshift({
          name: ''
        })
        this.currentCity = this.cityList[1].name
        this.currentSelectCity = 1
        this._getShopData(provinceId, this.currentSelectCity)
      })
    },
    _getShopData(provinceId, cityId) {
      getShopData(provinceId, cityId).then((res) => {
        this.$store.dispatch('waitingEnd', false)
        this.shopDatail = res.list
        this.shopDatail.length === 0 ? this.isShow = true : this.isShow = false
      })
    }
  },
  watch: {
    currentSelectProvince() { //
      const select = this.$refs.provinceSelect
      let index = select.selectedIndex
      if (index > 0) {
        let value = select.children[index].innerHTML
        this.currentProvince = value
      } else if (index === 0) {
        if (!this.currentSelectProvince) {
          let value = select.children[index].innerHTML
          this.currentProvince = value
          this.cityList = this.cityList.slice(0, 1)
          this.currentCity = ''
        }
      }
      if (select.children[index]) {
        let value = select.children[index].getAttribute('value')
        if (value) this._getCityData(value)
      }
    },
    provinceId() {
      this.currentSelectProvince = this.provinceId
      let option = this.$refs.provinceSelect.children
      option = Array.from(option)
      let locationProvince = ''
      option.forEach((e, i) => {
        if (e.getAttribute('value') === this.provinceId) {
          locationProvince = e.innerHTML
        }
      })
      this.currentProvince = locationProvince
    },
    currentSelectCity() { //
      console.log(this.currentSelectProvince);
      if (this.currentSelectCity === 1) {
        this.currentCity = this.cityList[1].name
        this._getShopData(this.currentSelectProvince, this.currentSelectCity)
        return
      }
      const select = this.$refs.citySelect
      let index = select.selectedIndex
      let value = select.children[index].innerHTML
      this.currentCity = value
      if (this.currentSelectCity) {
        this._getShopData(this.currentSelectProvince, this.currentSelectCity)
      }
    }
  },
  components: {
    MHeader
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/mixin"
  @import "~common/stylus/variable"

  .shop
    .m-header
      .logo
        float: left
        display: inline-block
        vertical-align: top
        margin: 14px 0 0 14px
        width: 144px
        height: 22px
        bg-image('logo')
        background-size: 144px 22px
        background-repeat no-repeat
      .user
        float: right
        display: inline-block
        vertical-align: top
        width: 50px
        height: 50px
        line-height: 50px
        text-align: center
        .user-button
          font-size: $font-size-large-x
    
    .banner
      .image
        display: block
        width: 100%

    .shop-select
      height: 50px
      display: flex
      background: #fff
      .province
        flex 1
        position: relative
        .province-show
          position: relative
          height: 50px
          line-height: 50px
          text-align: center
          border-right-1px(#f6f4f8)
          box-sizing border-box
          .text
            font-size: $font-size-large
            color: #a4a4a4
          .select-btn
            position: absolute
            right: 12px
            top: 0
            font-size: $font-size-large-x
        .province-select
          position: absolute
          left: 0
          top: 0
          width: 100%
          height: 50px
          border: 0
          outline: 0
          appearance: none
          opacity 0
      .city
        flex 1
        position: relative
        .city-show
          position: relative
          height: 50px
          line-height: 50px
          text-align: center
          .text
            display: block
            width: 80%
            overflow: hidden
            text-overflow: ellipsis
            white-space: nowrap
            font-size: $font-size-large
            color: #a4a4a4
          .select-btn
            position: absolute
            right: 12px
            top: 0
            font-size: $font-size-large-x
        .city-select
          position: absolute
          left: 0
          top: 0
          width: 100%
          height: 50px
          border: 0
          outline: 0
          appearance: none
          opacity 0

    .shop-show
      .shop-item
        margin-top: 10px
        background: #fff
        .shop-detail
          display: block
          padding: 20px
          border-bottom-1px(#f6f4f8)
          .shop-name
            color: #333
            line-height: 30px
          .shop-business-hours, .shop-phone, .shop-address
            line-height: 20px
            font-size: $font-size-medium 
        .shop-bottom
          height: 50px
          line-height: 50px
          display: flex
          a
            flex 1
            text-align: center
            &.shop-map
              border-right-1px(#f6f4f8)
            .iconfont
              margin-right: 4px
              font-size: 20px
              color: #ff3161
            .text
              color: #333

    .shop-noopend
      padding: 200px 0 
      text-align: center
      .text
        color: #3c363e
        font-size: $font-size-medium
</style>



