<template>
  <div class="user">

    <m-header>
      <router-link slot="user" tag="div" class="back" :to="fromPath">
        <i class="back-button iconfont icon-back"></i>
      </router-link>
      <h2 slot="cart" class="title">
        <span class="text"></span>
      </h2>
      <router-link slot="user" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
    </m-header>

    <div class="info">
      <div class="avatar">
        <img src="./user.jpg">
      </div>
      <p class="phone">13026321002</p>
      <p class="rank">
        <span>V1</span>
      </p>
    </div>

    <div class="order">
      <ul class="list">
        <li class="list-item">
          <router-link to="/">
            <i class="iconfont icon-allorder allorder"></i>
            <span class="text"></span>
          </router-link>
        </li>
        <li class="list-item">
          <router-link to="/">
            <i class="iconfont icon-unpaid unpaid"></i>
            <span class="text"></span>
          </router-link>
        </li>
        <li class="list-item">
          <router-link to="/">
            <i class="iconfont icon-unsend unsend"></i>
            <span class="text"></span>
          </router-link>
        </li>
        <li class="list-item">
          <router-link to="/">
            <i class="iconfont icon-unreceived unreceived"></i>
            <span class="text"></span>
          </router-link>
        </li>
      </ul>
    </div>

    <div class="munu">
      <p class="munu-item">
        <router-link to="/">
          <i class="iconfont icon-voucher voucher"></i>
          <span class="text"></span>
          <span class="enter">
            <i class="iconfont icon-back"></i>
          </span>
        </router-link>
      </p>

      <p class="munu-item">
        <router-link to="/">
          <i class="iconfont icon-alert alert"></i>
          <span class="text"></span>
          <span class="enter">
            <i class="iconfont icon-back"></i>
          </span>
        </router-link>
      </p>

      <p class="munu-item">
        <router-link to="/">
          <i class="iconfont icon-vip vip"></i>
          <span class="text">VIP</span>
          <span class="enter">
            <i class="iconfont icon-back"></i>
          </span>
        </router-link>
      </p>

      <p class="munu-item">
        <router-link to="/">
          <i class="iconfont icon-service service"></i>
          <span class="text"></span>
          <span class="enter">
            <i class="iconfont icon-back"></i>
          </span>
        </router-link>
      </p>

      <p class="munu-item">
        <router-link to="/">
          <i class="iconfont icon-map address"></i>
          <span class="text"></span>
          <span class="enter">
            <i class="iconfont icon-back"></i>
          </span>
        </router-link>
      </p>

      <p class="munu-item" @touchstart="signout">
        <a>
          <i class="iconfont icon-signout signout"></i>
          <span class="text"></span>
          <span class="enter">
            <i class="iconfont icon-back"></i>
          </span>
        </a>
      </p>
    </div>

    <transition name="fade">
      <modal v-show="isModal" @sure="sureSignout" @cancel="cancelSignout" title=""></modal>
    </transition>

    <div class="mask" @touchmove.prevent v-show="isModal"></div>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import Modal from 'base/modal/modal'

export default {
  data() {
    return {
      isModal: false,
      fromPath: ''
    }
  },
  created() {
    this.$store.dispatch('gWaitingStart', true)
    setTimeout(() => {
      this.$store.dispatch('gWaitingStart', false)
    }, 500)
  },
  activated() {
    this.$el.nextElementSibling.nextElementSibling.style.display = 'none'
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fromPath = from.path
    })
  },
  methods: {
    signout() {
      window.scrollTo(0, 0)
      this.isModal = true
    },
    sureSignout() {
      localStorage.removeItem('user')
      this.isModal = false
      this.$router.push('/login')
      this.$store.dispatch('gWaitingStart', true)
      setTimeout(() => {
        this.$store.dispatch('gWaitingStart', false)
      }, 500)
    },
    cancelSignout() {
      this.isModal = false
    }
  },
  components: {
    MHeader,
    Modal
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"

  .user
    position: relative
    .m-header
      line-height: 50px
      text-align: center
      font-size: 0
      .back
        display: inline-block
        float: left
        width: 50px
        height: 50px
        text-align: center
        .back-button
          font-size: $font-size-large-x
      .title
        display: inline-block
        height: 50px
        .text
          font-size: 20px
          color: #3c363e
      .home
        display: inline-block
        float: right
        width: 50px
        height: 50px
        text-align: center
        .icon-home
          font-size: $font-size-large-x

    .info
      padding: 20px 0
      border-top: 1px solid #f6f4f8
      background: #fff
      .avatar
        margin: 0 auto
        width: 100px
        height: 100px
        img
          display: block
          width: 100%
          box-sizing: border-box
          border: 1px solid #ff71aa
          border-radius: 50%
      .phone
        height: 30px
        line-height: 30px
        text-align: center
        color: #3c363e
      .rank
        height: 28px
        line-height: 28px
        text-align: center
        span
          display: inline-block
          height: 28px
          width: 80px
          border-radius: 20px
          background: #ff71aa
          color: #3c363e

    .order
      padding: 20px 0
      background: #fff
      border-top: 1px solid #f6f4f8
      .list
        display: flex
        .list-item
          flex: 1
          a
            display: block
            i
              display: block
              width: 30px
              height: 30px
              line-height: 30px
              text-align: center
              margin: 0 auto 10px
              font-size: 30px
            .text
              display: block
              text-align: center
              color: #3c363e
          &:nth-child(1) .allorder
            color: #efb336
          &:nth-child(2) .unpaid
            color: #068afb
          &:nth-child(3) .unsend
            color: #1afa29
          &:nth-child(4) .unreceived
            color: #f04108
              
    .munu
      .munu-item
        border-top: 1px solid #f6f4f8
        background: #fff
        a
          display: block
          overflow: hidden
          padding: 20px
          i
            display: block
            float: left
            margin-right: 8px  
            width: 25px
            height: 25px
            line-height: 25px
            text-align: center
            font-size: 20px
          .text
            display: block
            height: 25px
            line-height: 25px
            float left
            color: #3c363e
          .enter
            display: block
            float: right
            width: 25px
            height: 25px
            line-height: 25px
            text-align: center
            transform: rotate(180deg)
            i
              font-size: $font-size-medium
        &:nth-child(1) .voucher
          color: #f04108
        &:nth-child(2) .alert
          color: #06c6fb
        &:nth-child(3) .vip
          color: #f4ea2a
        &:nth-child(4) .service
          color: #5dc710
        &:nth-child(5) .address
          color: #06c6fb
        &:nth-child(6) .signout
          color: #bd68ff

  .mask
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100%
    background: rgba(0, 0, 0, 0.5)

  .fade-enter-active, .fade-leave-active
    transition: opacity 0.5s

  .fade-enter, .fade-leave-to
    opacity: 0

</style>





