<template>
  <div id="detail">

    <m-header>
      <router-link slot="detail" tag="div" class="back" :to="fromPath">
        <i class="back-button iconfont icon-back"></i>
      </router-link>
      <router-link tag="h1" to="/" slot="detail" class="logo"></router-link>
      <router-link slot="detail" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
    </m-header>

    <h2 class="good-title border-tb-1px">
      <p class="text">{{title}}</p>
    </h2>

    <div class="slider-wrapper bg" ref="sliderWrapper">
      <slider v-if="sliderImages.length" :loop="isLoop" :autoPlay="false" :checkIndex="checkIndex">
        <div v-for="(item,index) in sliderImages" :key="index">
          <a href="javascript:;" style="width:50%; margin:0 auto;">
            <img :src="item.image" @load="imageLoad" v-if="index === 0">
            <img :src="item.image" v-else-if="index !== 0">
            <p style="height:12px;"></p>
          </a>
        </div>
      </slider>
    </div>

    <div class="specification">
      <p class="text"></p>
      <div class="type">
        <button class="type-item" :class="{active:checkIndex === index}" v-for="(item,index) in sliderImages" :key="index" @touchstart="checkGood(index)">{{item.name}}</button>
      </div>
    </div>

    <div class="quantity">
      <p class="text"></p>
      <div class="change" ref="change">
        <i class="iconfont icon-decrease decrease" :class="{active:decreaseActive}" @touchstart="decreaseGood" @touchend="decreaseActive = false"></i>
        <span class="number">{{num}}</span>
        <i class="iconfont icon-increase increase" :class="{active:increaseActive}" @touchstart="increaseGood" @touchend="increaseActive = false"></i>
      </div>
    </div>

    <div class="good-price" v-if="sliderImages.length">
      <p class="price">
        <span class="rmb">RMB</span>
        <span class="num">{{price}}</span>
      </p>
    </div>

    <div class="good-detail">
      <h2 class="title"></h2>
      <div class="image-wrapper" v-for="(item,index) in detailImages" :key="item">
        <img v-lazy="item">
      </div>
    </div>

    <p style="height:60px;"></p>

    <div class="good-car">
      <router-link class="small-car" to="/cart" tag="div">
        <div class="icon" ref="icon">
          <i class="cart-button iconfont icon-cart"></i>
          <em class="cart-count">{{carNum}}</em>
        </div>
      </router-link>
      <div class="add-car" @touchstart="addCart">
        <span class="text"></span>
      </div>
      <div class="buy-now">
        <span class="text"></span>
      </div>
    </div>

    <transition name="fade">
      <warn class="warn" :title="titleData" v-show="isWarn"></warn>
    </transition>

    <transition name="ball" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
      <div class="ball" v-show="isBall"></div>
    </transition>

  </div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import Slider from 'base/slider/slider'
import Warn from 'base/warn/warn'
import { getGoodDetail } from 'api'

export default {
  name: 'detail',
  data() {
    return {
      sliderImages: [],
      title: '',
      detailImages: [],
      checkIndex: 0,
      price: 0,
      num: 0,
      decreaseActive: false,
      increaseActive: false,
      isLoop: true,
      isWarn: false,
      titleData: '',
      isBall: false,
      fromPath: '',
      count: 0,
      goodIdArr: [],
      refresh: 0
    }
  },
  created() {
    let goodId = this.$route.params.goodId
    this.goodIdArr.push(goodId)
    this._getGoodDetail(goodId)
    this._setLocalStorage()
  },
  activated() {
    window.scrollTo(0, 0)
    let goodId = this.$route.params.goodId
    if (!this.goodIdArr.includes(goodId)) { // goodIdArrgoodIdvuex
      this.goodIdArr.push(goodId)
      this._getGoodDetail(goodId)
      this._setLocalStorage()
    } else {
      let detailData = this.$store.getters.detailData
      if (detailData.length) {
        let goodId = this.$route.params.goodId
        let filterData = detailData.filter((e) => {
          return e.product.id == goodId
        })

        this.checkIndex = 0
        this.sliderImages = filterData[0].specification
        this.price = filterData[0].specification[this.checkIndex].price
        this.title = filterData[0].product.title
        this.detailImages = filterData[0].product.images
        if (this.sliderImages.length === 1) this.isLoop = false
      }
    }
  },
  deactivated() { // 
    this.sliderImages = []
    this.title = ''
    this.detailImages = []
    this.price = 0
    this.num = 0
    this.decreaseActive = false
    this.increaseActive = false
    this.isLoop = true
    this.isWarn = false
    this.titleData = ''
    this.isBall = false
    this.fromPath = ''
    this.count = 0
    this.$refs.sliderWrapper.className = 'slider-wrapper bg'
  },
  mounted() {
    this.$el.nextElementSibling.nextElementSibling.style.display = 'none'
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fromPath = from.path
    })
  },
  methods: {
    imageLoad() {
      this.$refs.sliderWrapper.className = 'slider-wrapper'
    },
    checkGood(index) {
      this.checkIndex = index
      this.num = 0
    },
    decreaseGood() {
      this.num--
      if (this.num < 0) this.num = 0
      this.decreaseActive = true
    },
    increaseGood() {
      this.num++
      this.increaseActive = true
      if (this.num > 3) {
        this.num = 3
        this.titleData = ''
        this.isWarn = true
        setTimeout(() => {
          this.isWarn = false
        }, 1000)
      }
    },
    addCart() {
      if (!this.num) {
        this.titleData = ''
        this.isWarn = true
        setTimeout(() => {
          this.isWarn = false
        }, 1000)
        return
      }
      this.count = this.carNum
      this.$store.dispatch('addCart', {
        id: this.sliderImages[this.checkIndex].id,
        title: this.title,
        image: this.sliderImages[this.checkIndex].image,
        name: this.sliderImages[this.checkIndex].name,
        price: this.sliderImages[this.checkIndex].price,
        num: this.num,
        check: false,
        path: this.$route.path
      })
      if (this.count === this.carNum) {
        this.titleData = ''
        this.isWarn = true
        setTimeout(() => {
          this.isWarn = false
        }, 1000)
      } else {
        this.isBall = true
      }
      this.num = 0
    },
    beforeEnter(el) {
      let l = this.$refs.change.getBoundingClientRect().left
      let t = this.$refs.change.getBoundingClientRect().top
      el.style.left = l + 35 + 'px'
      el.style.top = t + 5 + 'px'
      el.style.transform = 'translate(0, 0)'
      el.style.backgroundColor = 'rgba(239, 179, 54, 1)'
    },
    enter(el, done) {
      el.offsetWidth // el.offsetWidthel.offsetWidth
      let translateX = this.$refs.icon.getBoundingClientRect().left - el.getBoundingClientRect().left + 'px'
      let translateY = this.$refs.icon.getBoundingClientRect().top - el.getBoundingClientRect().top + 'px'
      el.style.transform = `translate(${translateX}, ${translateY})`
      el.style.backgroundColor = 'rgba(245, 86, 105, 0.5)'
      done()
    },
    afterEnter(el) {
      this.isBall = false
    },
    _getGoodDetail(goodId) {
      this.$store.dispatch('gWaitingStart', true) // loading
      getGoodDetail(goodId).then((res) => {
        if (res) {
          this.$store.dispatch('storeDetailData', res.response.data) // vuex
          this.$store.dispatch('gWaitingEnd', false) // loading

          this.checkIndex = 0
          this.sliderImages = res.response.data.specification
          this.price = res.response.data.specification[this.checkIndex].price
          this.title = res.response.data.product.title
          this.detailImages = res.response.data.product.images
          if (this.sliderImages.length === 1) this.isLoop = false
          this.refresh++
        }
      })
    },
    _setLocalStorage() {
      let localStorageData = JSON.parse(localStorage.getItem('goods')) || []
      this.$store.dispatch('restore', {
        localStorageData
      })
    }
  },
  computed: {
    carNum() {
      return this.$store.getters.cartTotal
    }
  },
  watch: {
    checkIndex(val) {
      this.price = this.sliderImages[val].price
    }
  },
  components: {
    MHeader,
    Slider,
    Warn
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/mixin"
  @import "~common/stylus/variable"

  #detail
    .m-header
      line-height: 50px
      text-align: center
      font-size: 0
      background-color: #000
      .back
        display: inline-block
        float: left
        width: 50px
        height: 50px
        text-align: center
        .back-button
          font-size: $font-size-large-x
      .logo
        display: inline-block
        margin: 14px 0 0 14px
        width: 144px
        height: 22px
        bg-image('logo')
        background-size: 144px 22px
        background-repeat no-repeat
      .home
        display: inline-block
        float: right
        width: 50px
        height: 50px
        text-align: center
        .icon-home
          font-size: $font-size-large-x
        
    .good-title
      background: #fff
      border-bottom-1px(#f6f4f8)
      .text
        height: 50px
        line-height: 50px
        color: #333
        font-size: $font-size-large
        text-indent: 20px

    .slider-wrapper
      min-height: 200px
      background-color: #fff
      &.bg
        bg-image('bg')
        background-size 85px 85px
        background-repeat no-repeat
        background-position center center


    .specification
      background: #fff
      .text
        height: 30px
        line-height: 30px
        text-indent: 20px
        font-size: $font-size-medium-x
        color: #333
      .type
        padding: 20px 20px 20px
        overflow: hidden
        .type-item
          width: 45%
          height: 40px
          margin-bottom: 10px
          border: 1px solid #cfcfcf
          border-radius: 6px
          background: none
          font-size: $font-size-medium-x
          color: #333
          &:nth-child(odd)
            float: left
          &:nth-child(even)
            float: right
            margin-right: 0
          &.active
            border: 1px solid #f55669

    .quantity
      position: relative
      .text
        height: 30px
        line-height: 30px
        padding: 20px 0
        text-indent: 20px
        font-size: $font-size-medium-x
        color: #333
      .change
        position: absolute
        right: 20px
        bottom: 20px
        font-size: 0
        .decrease
          display: inline-block
          vertical-align: middle
          width: 30px
          height: 30px
          line-height: 30px
          text-align: center
          font-size: 26px
          color: #cfcfcf
          &.active
            color: #f55669
        .number
          display: inline-block
          vertical-align: middle
          width: 30px
          height: 30px
          line-height: 32px
          text-align: center
          font-size: 18px
          color: #2a242b
        .increase
          display: inline-block
          vertical-align: middle
          width: 30px
          height: 30px
          line-height: 30px
          text-align: center
          font-size: 26px
          color: #cfcfcf
          &.active
            color: #f55669
    .good-price
      position: relative
      overflow: hidden
      padding: 0 20px
      .price
        float: right
        height: 50px
        margin-right: 32px
        line-height: 50px
        .rmb
          color: #f55669
          font-size: $font-size-large
        .num
          position: absolute
          right: 20px
          top: 0
          color: #f55669
          font-size: $font-size-large

    .good-detail
      background: #fff
      .title
        height: 50px
        line-height: 50px
        text-align: center
        color: #333
        font-size: $font-size-large
        border-bottom-1px(#f6f4f8)
      .image-wrapper
        img
          display: block
          width: 100%

    .good-car
      position: fixed
      left: 0
      bottom: 0
      width: 100%
      display: flex
      height: 60px
      line-height: 60px
      .small-car
        position: relative
        flex: 1
        background: #fff
        .icon
          position: absolute
          left: 0
          right: 0
          top: 0
          bottom: 0
          margin: auto
          width: 50px
          .cart-button
            display: block
            font-size: $font-size-large-x
          .cart-count
            position: absolute
            left: 0
            top: 2px
            display: block
            width: 18px
            height: 18px
            line-height: 18px
            text-align: center
            border-radius: 50%
            font-size: $font-size-medium
            font-style: normal
            color: #fff
            background: #f55669
      .add-car
        flex: 1
        text-align: center
        background: #efb336
        .text
          color: #fff
          font-size: $font-size-large
      .buy-now
        flex: 1
        text-align: center
        background: #f55669
        .text
          color: #fff
          font-size: $font-size-large

    .warn
      position: fixed
    
    .ball
        position: fixed
        left: 0
        top: 0
        width: 20px
        height: 20px
        background: #f55669
        border-radius: 50%
        transition: all 0.5s ease
        z-index: 1000

    .fade-enter-active, .fade-leave-active
      transition: opacity 0.5s

    .fade-enter, .fade-leave-to
      opacity: 0

</style>



