<template>
  <div class="category border-tb-1px" ref="category">
    <div class="category-group" ref="categoryGroup">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BetterScroll from 'better-scroll'
import { addClass } from 'common/js/dom'

export default {
  mounted() {
    setTimeout(() => {
      this._setCategoryWidth()
      this._initCategory()
    }, 20)

    window.addEventListener('resize', () => { // refresh()
      if (!this.Category) {
        return
      }
      this._setCategoryWidth(true)
      this.Category.refresh()
    })
  },
  methods: {
    _setCategoryWidth(isResize) {
      this.children = this.$refs.categoryGroup.children[0].children
      let width = 0
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        // class
        addClass(child, 'category-item')
        width += child.offsetWidth + 2
      }
      this.$refs.categoryGroup.style.width = width + 'px'
    },
    _initCategory() { // 
      this.category = new BetterScroll(this.$refs.category, {
        scrollX: true,
        scrollY: false,
        momentum: true,
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/mixin"
  
  .category
    min-height: 1px
    overflow: hidden
    border-bottom-1px(#f6f4f8)
    .category-group
      position: relative
      .category-list
        overflow: hidden
        padding: 10px 0
        .category-item
          float: left
          margin-right: 2px
          &:last-child, &:first-child
            margin-right: 0
          a
            display: block
            padding: 4px
            color: #444
            &.router-link-active
              background: #f55669
              color: #fff
              border-radius 4px
        .category-title
          padding: 4px 0 4px 10px
          color: #444

</style>



