<template>
  <div class="recommend" ref="recommend">
    <div class="recommend-group" ref="recommendGroup">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import BetterScroll from 'better-scroll'
import { addClass } from 'common/js/dom'

export default {
  mounted() {
    setTimeout(() => {
      this._setRecommendWidth()
      this._initRecommend()
    }, 20)

    window.addEventListener('resize', () => { // refresh()
      if (!this.recommend) {
        return
      }
      this._setRecommendWidth(true)
      this.recommend.refresh()
    })
  },
  activated() {
    if (!this.recommend) {
      return
    }
    this._setRecommendWidth(true)
    this.recommend.refresh()
  },
  methods: {
    _setRecommendWidth(isResize) {
      this.children = this.$refs.recommendGroup.children
      let width = 0
      let recommendWidth = this.$refs.recommend.clientWidth * 0.75
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i]
        // class
        addClass(child, 'recommend-item')
        child.style.width = recommendWidth + 'px'
        width += recommendWidth
      }
      this.$refs.recommendGroup.style.width = width + 30 + 'px'
    },
    _initRecommend() { // 
      this.recommend = new BetterScroll(this.$refs.recommend, {
        scrollX: true,
        scrollY: false,
        momentum: true,
        eventPassthrough: 'vertical'
      })
    }
  }

}
</script>

<style lang="stylus" scoped>
  
  .recommend
    min-height: 1px
    position: relative
    overflow: hidden
    .recommend-group
      position: relative
      white-space: nowrap
      .recommend-item
        float: left
        margin-right: 10px
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
        img
          display: block
          width: 100%
          

</style>




