<template>
  <div class="modal">
    <h3 class="title"></h3>
    <em class="line"></em>
    <p class="text">{{title}}</p>
    <button class="cancel" @touchstart="cancel"></button>
    <button class="sure" @touchstart="sure"></button>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    }
  },
  methods: {
    sure() {
      this.$emit('sure')
    },
    cancel() {
      this.$emit('cancel')
    },
  }
}
</script>

<style lang="stylus" scoped>
  .modal
    position: fixed
    left: 0
    top: 0
    right: 0
    bottom: 0
    margin: auto
    width: 300px
    height: 150px
    padding: 20px
    text-align: center
    background: #fff
    border-radius: 10px
    z-index: 10000
    .title
      text-align: center
      margin-bottom: 20px
      color: #000
      font-size: 20px
    .line
      display: block
      width: 100%
      height: 1px
      margin-bottom: 10px
      background: #f6f4f8
    .text
      text-align: center
      height: 30px
      line-height: 30px
      margin-bottom: 20px
      font-size: 14px
      color: #000
    button
      width: 140px
      height: 40px
      border: 1px solid #f55669
      background: #fff
      font-size: 16px
    .cancel
      margin-right: 10px
      color: #f55669
    .sure
      background: #f55669
      color: #fff

</style>


