<template>
  <div class="services">

    <m-header>
      <router-link slot="services" tag="div" class="back" to="/">
        <i class="back-button iconfont icon-back"></i>
      </router-link>
      <h2 slot="services" class="title">
        <span class="text"></span>
      </h2>
      <router-link slot="services" tag="div" class="home" to="/">
        <i class="home-button iconfont icon-home"></i>
      </router-link>
    </m-header>

    <div class="services-phone">
      <div class="icon"></div>
      <div class="info">
        <p class="tel">400-990-9696</p>
        <p class="time"> 9:00-24:00</p>
      </div>
    </div>

    <div class="class-menu">
      <div class="item">
        <router-link class="link" to="/">
          
        </router-link>
      </div>

      <div class="item">
        <router-link class="link" to="/">
          
        </router-link>
      </div>

      <div class="item">
        <router-link class="link" to="/">
          
        </router-link>
      </div>
    </div>

    <services-list :servicesListData="item" v-for="item in servicesList" :key="item.id"></services-list>

</div>
</template>

<script>
import MHeader from 'base/m-header/m-header'
import ServicesList from 'components/services/services-list'
import { getSupportsData } from 'api'

export default {
  data() {
    return {
      servicesList: []
    }
  },
  created() {
    this._getSupportsData()
  },
  methods: {
    _getSupportsData() {
      this.$store.dispatch('waitingStart', true)
      getSupportsData().then((res) => {
        this.$store.dispatch('waitingEnd', false)
        this.servicesList = res.result.list
      })
    }
  },
  components: {
    MHeader,
    ServicesList
  }
}
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  
  .services
    .m-header
      line-height: 50px
      text-align: center
      font-size: 0
      .back
        display: inline-block
        float: left
        width: 50px
        height: 50px
        text-align: center
        .back-button
          font-size: $font-size-large-x
      .title
        display: inline-block
        height: 50px
        .text
          font-size: 20px
          color: #3c363e
      .home
        display: inline-block
        float: right
        width: 50px
        height: 50px
        text-align: center
        .icon-home
          font-size: $font-size-large-x

    .services-phone
      padding: 10px
      margin-top: 6px
      background: #fff
      .icon
        display: inline-block
        vertical-align: middle
        width: 100px
        height: 100px
        margin-right: 10px
        bg-image('icon')
        background-size 100px 100px
        background-repeat no-repeat
      .info
        display: inline-block
        vertical-align: middle
        line-height: 30px
        color: #000
        .tel
          font-size: 20px
        .time
          color: #817b82

    .class-menu
      margin: 10px 0
      padding: 6px
      display: flex
      background: #fff
      .item
        flex 1
        height: 80px
        line-height: 80px
        margin-right: 6px
        border-radius 6px
        &:first-child
          bg-image('feedback')
          background-size auto 80px
          background-repeat repeat-x
        &:nth-child(2)
          bg-image('experience')
          background-size auto 80px
          background-repeat repeat-x
        &:last-child
          margin-right: 0
          bg-image('branch')
          background-size auto 80px
          background-repeat repeat-x
        a
          display: block
          text-align: center
          color: #fff

</style>




